<template>
<div class="row" v-if="ready">
    <!--right-->
    <div data-app class="col-lg-8">
        <label>User Management</label>
            <div class="form-group">
                <div class="col-12 px0 flex" style="margin-bottom:1rem">
                    <div class="col-6 px0">
                        <input type="text" class="form-control" v-model="search_user_key" @keyup="search_user" placeholder="Search User"/>
                    </div>  
                    <div class="col-2"></div>
                    <div class="col-4 px0">
                        <select class="form-control select" v-model="rank">
                            <option value="" disabled>Please Select Rank Below</option>
                            <option value="user">User</option>
                            <option value="secretary">Secretary</option>
                            <option value="admin">Admin</option>
                        </select>
                    </div>                 
                </div>
                    <v-data-table
                    :headers="headers_list"
                    :items="userdata"
                    :search="search"
                    class="elevation-1"
                    :rows-per-page-items="[10]"
                    v-if="hv_user"
                    >
                <template v-slot:items="props">
                    <td><a href="" v-on:click.prevent="add_user(props.item.username,props.item.name)">Add</a></td>
                    <td>{{ props.item.name }}</td>
                    <td>{{ props.item.username }}</td>   
                <template v-slot:pageText="props">
                    Items {{ props.pageStart }} - {{ props.pageStop }} out of {{ props.itemsLength }}
                </template>              
                </template>
                </v-data-table>     
                <!--live search-->           
            </div>                       
            <v-data-table
            v-if="userlist_ready"
            :headers="headers"
            :items="userlist"
            class="elevation-1"
            :rows-per-page-items="[10]"
            >
            <template v-slot:items="props">
                <td><a href="" v-on:click.prevent="remove_user(props.item.username,props.item.name)">Remove</a></td>
                <td>{{ props.item.name }}</td>
                <td>{{ props.item.username }}</td> 
                <td>{{ props.item.permission }}</td> 
            <template v-slot:pageText="props">
                Items {{ props.pageStart }} - {{ props.pageStop }} out of {{ props.itemsLength }}
            </template>        
            </template>
            </v-data-table><br/>
            <!--member-->
    </div>
</template>
<script>
export default {
    data(){
        return{
            ready:false,
            rank:'',
            title:'Member',
            title_dep:['Secretary','Member'],
            search_user_key:"",
            userlist:[],
            userdata:[],
            userlist_ready:false,
            hv_user:false,
            headers: [
            { text: 'Action',value :'action',sortable: false},
            { text: 'Name',value:'name',sortable: false},
            { text: 'Username',value:'username',sortable: false},
            { text: 'Rank',value:'permission',sortable: false},
            ], 
            headers_list: [
            { text: 'Action',value :'action',sortable: false},
            { text: 'Name',value:'name',sortable: false},
            { text: 'Username',value:'username',sortable: false},
            ],
        }
    },
async created(){
    await this.fetch_user();
    this.ready = true;
},
methods:{
    async fetch_user(){
        await axios.get('inventory/web/backend/inv_admin_list.php')
            .then(
                (response)=>{
                    if(response.data!='empty'){
                        this.userlist = response.data;
                    } else {
                        this.userlist = [];
                    }
                }
            )
        this.userlist_ready = true;
        this.$forceUpdate();
    },
    async add_user(uid,name){
        var formData = new FormData();
        formData.append('username',uid);
        formData.append('name',name);
        formData.append('rank',this.rank);
        if(this.rank!=''){
            await axios.post('inventory/web/backend/add_inv_admin.php',formData)
                .then(
                    (response)=>{
                        if(response.data=='OK'){    
                            this.fetch_user();                  
                            this.search_user_key = '';
                            this.hv_user = false;
                        }
                    }
                )   
            this.$alert('Successfully added user : '+name+' as '+this.rank);
        } else {
            this.$alert('Please Select Rank');
        }
    },
    search_user(){
        var formData = new FormData();
        formData.append('key', this.search_user_key);
        axios.post('RequestPortal/backend/search_user_list.php',formData)
            .then(
                (response)=>{
                    if(response.data == "Empty"){
                       this.hv_user=false; 
                    } else {
                        this.userdata = response.data;
                        this.userdata.id = response.data.id;
                        this.userdata.username = response.data.username;
                        this.userdata.name = response.data.name;
                        this.hv_user=true;
                    }
                }); 
    },
    async remove_user(uid,name){
        this.$confirm('Confirm Removing User '+name+ ' ?')
            .then(
                async()=>{
                    var formData = new FormData();
                    formData.append('uid',uid);
                    await axios.post('inventory/web/backend/remove_inv_admin.php',formData)
                        .then(
                            (response)=>{
                                if(response.data=='OK'){
                                    this.$alert('Successfully Removed User ' + name);
                                    this.fetch_user();
                                }
                            }
                        )
                }
            )      
    }
}
}
</script>
<style scoped>
.px0{
    padding-left:0;
    padding-right:0;
}
.flex{
    display:flex;
    flex-direction: row;
}
</style>



